.mobile-navigation {
    display: flex;
    align-items: center;
    background: var(--secondary-color);
    color: var(--main-bg-color);
    height: 100%;

    button {
        border: none;
        background-color: unset;

        svg {
            width: 40px;
            height: 40px;

            &>g {
                transform: translate(0px, 100px);
            }

            path {
                fill: var(--secondary-bg-color);
            }
        }
    }

    .restaurant-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding-right: 10px;

        span {
            text-transform: uppercase;
        }
    }

    &__wrapper {
        z-index: 3;
        position: sticky;
        top: 0;
        right: 0;
        left: 0;
        height: 60px;
        margin-bottom: 10px;
    }
}

.mobile-menu {
    color: var(--main-bg-color);
    height: 100%;

    .mt-25 {
        margin-top: 25%;
    }

    &__wrapper {
        position: fixed;
        top: 0;
        bottom: 0;
        left: -100%;
        width: 100%;
        transition: left ease-in-out 0.5s;
        background: rgba(0, 0, 0, 0.95);

        &.active {
            left: 0;
        }
    }

    &__header {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 90px;

        button {
            border: 0;
            background-color: unset;
            color: var(--main-bg-color);
            font-size: 30px;
            line-height: 30px;
            padding: 30px;
        }
    }

    &__navigation {
        display: flex;
        justify-content: center;
        text-align: center;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                padding: 30px
            }
        }
    }

    &__footer {
        display: flex;
        justify-content: center;
        height: 50px;
        padding-top: 20px;

        svg {
            width: 30px;
            height: 30px;
            margin: 0 5px;

            path {
                fill: var(--main-bg-color)
            }
        }
    }
}